html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  padding: 0 calc((100% - 320px) / 2);
}
@media (min-width: 768px) {
  .page {
    padding: 0 calc((100% - 768px) / 2);
  }
}
@media (min-width: 1440px) {
  .page {
    padding: 0 calc((100% - 1440px) / 2);
  }
}

@media (min-width: 768px) {
  .header {
    padding: 0 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.header__logo {
  display: block;
  padding-top: 20px;
  padding-bottom: 11px;
